<template>
  <div class="hello">

    <img  v-if="this.search === 'confirmados'" src="../assets/infected.jpg"/>
    <img  v-if="this.search === 'mortos'" src="../assets/death.png"/>
    <img  v-if="this.search === 'recuperados'" src="../assets/recovery.jpg"/>

    <h1>{{this.search}}</h1>

    <h3>Data atual {{currentTime}} </h3>
    <table>
      <tr>
        <th>Lugar</th>
        <th>País</th>
        <th>{{this.search}}</th>
      </tr>
      
      <tr v-for="countryInfo in countryInfos" :key="countryInfo.country">
        <td>#{{countryInfo.rank}}</td>
        <td>{{countryInfo.country}}</td>
        <td>{{countryInfo.info}}</td> 
      </tr>
     
    </table>
  </div>
</template>

<script>
export default {
  name: "HelloWorld",
  props: {

    countries:Number,
    search:String

  },
  data(){
    return{
      covid:{},
      countryInfos:[],
      currentTime:""
    }
  },
async created(){
  const response= await fetch('https://api.covid19api.com/summary')
  const myData = await response.json()
  let countryInfos = []

  if(this.search == "confirmados"){
    for(let i = 0; i < myData.Countries.length; i++){
      countryInfos.push({
        country: myData.Countries[i].Country,
        info: myData.Countries[i].TotalConfirmed
      })
    }
  }else if(this.search =="mortos"){
    for(let i = 0; i < myData.Countries.length; i++){
      countryInfos.push({
        country: myData.Countries[i].Country,
        info: myData.Countries[i].TotalDeaths
      })
    }
  }else if(this.search == "recuperados"){
    for(let i = 0; i < myData.Countries.length; i++){
      countryInfos.push({
        country: myData.Countries[i].Country,
        info: myData.Countries[i].TotalRecovered
      })
    }
  }


  countryInfos.sort(function(a,b){return b.info - a.info})

  let finalResult = []

  for(let i = 0; i < this.countries; i++){
    finalResult.push({
      country: countryInfos[i].country,
      info: countryInfos[i].info
    })
  }

  var today = new Date()

  var dd = today.getDate();
  var mm = today.getMonth()+1; //As January is 0.
  var yyyy = today.getFullYear();

  if(dd<10) dd='0'+dd;
  if(mm<10) mm='0'+mm;

this.currentTime = `${dd}/${mm}/${yyyy}`


  this.countryInfos = finalResult
 
  //countryInfo = myData.Countries.find(result => result.Country == this.country)

  console.log("Checked covid into: ", countryInfos)
  
},

async updated(){
    const response= await fetch('https://api.covid19api.com/summary')
  const myData = await response.json()
  let countryInfos = []

  if(this.search == "confirmados"){
    for(let i = 0; i < myData.Countries.length; i++){
      countryInfos.push({
        country: myData.Countries[i].Country,
        info: myData.Countries[i].TotalConfirmed
      })
    }
  }else if(this.search =="mortos"){
    for(let i = 0; i < myData.Countries.length; i++){
      countryInfos.push({
        country: myData.Countries[i].Country,
        info: myData.Countries[i].TotalDeaths
      })
    }
  }else if(this.search == "recuperados"){
    for(let i = 0; i < myData.Countries.length; i++){
      countryInfos.push({
        country: myData.Countries[i].Country,
        info: myData.Countries[i].TotalRecovered
      })
    }
  }


  countryInfos.sort(function(a,b){return b.info - a.info})

  let finalResult = []

  for(let i = 0; i < this.countries; i++){
    finalResult.push({
      rank: i + 1,
      country: countryInfos[i].country,
      info: countryInfos[i].info
    })
  }

 var today = new Date()

  var dd = today.getDate();
  var mm = today.getMonth()+1; //As January is 0.
  var yyyy = today.getFullYear();

  if(dd<10) dd='0'+dd;
  if(mm<10) mm='0'+mm;

this.currentTime = `${dd}/${mm}/${yyyy}`

  this.countryInfos = finalResult

  //countryInfo = myData.Countries.find(result => result.Country == this.country)

  console.log("Checked covid into: ", countryInfos)
}


};
</script>

